import React, { Component } from 'react';
import 'leaflet';
import 'leaflet.markercluster';
import 'leaflet.markercluster/dist/MarkerCluster.css';
import 'leaflet.markercluster/dist/MarkerCluster.Default.css';
import 'leaflet-geosearch/dist/style.css';
import 'leaflet-geosearch/assets/css/leaflet.css';
import {
  GeoSearchControl,
  OpenStreetMapProvider,
} from 'leaflet-geosearch';


class LMap extends Component {
	constructor(props) {
		super(props);
		this.mountMap = this.mountMap.bind(this);
	}
	mountMap(element) {
		if (!element)
			return;
		const { L } = window;
		const provider = new OpenStreetMapProvider();
		const searchControl = new GeoSearchControl({
		  provider: provider,
		  autoCompleteDelay: 50,
		  showMarker: false,
		  showPopup: false,
  		autoClose: true
		});
		var greenIcon = L.icon({
		    iconUrl: `${process.env.PUBLIC_URL}/marker.png`,
		    iconSize:     [38, 95], // size of the icon
		    shadowSize:   [50, 64], // size of the shadow
		    iconAnchor:   [22, 94], // point of the icon which will correspond to marker's location
		    shadowAnchor: [4, 62],  // the same for the shadow
		    popupAnchor:  [-3, -76], // point from which the popup should open relative to the iconAnchor,
		    className: 'Hello'
		});
		var nishuIcon = L.icon({
		    iconUrl: `${process.env.PUBLIC_URL}/hati.png`,
		    iconSize:     [38, 95], // size of the icon
		    shadowSize:   [50, 64], // size of the shadow
		    iconAnchor:   [22, 94], // point of the icon which will correspond to marker's location
		    shadowAnchor: [4, 62],  // the same for the shadow
		    popupAnchor:  [-3, -76] // point from which the popup should open relative to the iconAnchor
		});
		const htmlIcon = L.divIcon({className: 'my-div-icon', html:'<div><span>Click me</span></div>'});
		const map = L.map(element).setView([40.706877, -74.011265], 13);
		L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
		    attribution: 'Map data &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors, <a href="http://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="http://mapbox.com">Mapbox</a>',
		    maxZoom: 18,
		    id: 'mapbox.streets',
		    accessToken: 'sk.eyJ1Ijoib25seXJlZmF0IiwiYSI6ImNqNmx1dWx2ZjFtYTUycXAwNmEydThiYWcifQ.OweLH0xgPuQeTJouiLYQwA'
		}).addTo(map);
		// var markers = L.markerClusterGroup();
		// markers.addLayer(L.marker([51.5, -0.09], {icon: greenIcon})
		//     .bindPopup('<div>Marker With<br> <span>Image</span>.</div>')
		//     .openPopup());
		// markers.addLayer(L.marker([51.493, -0.1259], {icon: nishuIcon})
		//     .bindPopup('Lungi')
		//     .openPopup());
		// markers.addLayer(L.marker([51.5076, -0.1237])
		//     .bindPopup('Simple Marker with Default icon')
		//     .openPopup());
		// markers.addLayer(L.marker([51.5076, -0.1237])
		//     .bindPopup('Simple Marker with Default icon 2')
		//     .openPopup());
		// markers.addLayer(L.marker([51.508, -0.0619], {icon: htmlIcon})
		// 		.bindPopup('Simple Marker Using Div Element')
		//     .openPopup());
		// map.addLayer(markers);
		map.addControl(searchControl);
		map.on('geosearch/showlocation', function(result) {
			const marker = L.marker([result.location.y, result.location.x], { draggable: true }).addTo(map);
			const popup = L.popup({closeOnClick: false}).setContent(`${result.location.y}, ${result.location.x}`);
			marker.bindPopup(popup).openPopup();
			marker.on('dragend', function(data) {
        var marker1 = data.target;
        L.DomEvent.stopPropagation(data);
        var position = marker1.getLatLng();
        marker.setLatLng(position,{draggable:'true'}).bindPopup(`${position.lat}, ${position.lng}`).openPopup();
			})
		});
	}
  render() {
    return (
      <div className="App">
      	<div id="map" style={{ height: '800px', width: '100%' }} ref={this.mountMap} />
      </div>
    );
  }
}

export default LMap;



